import React, { useState, useRef, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import ReactDice from 'react-dice-complete';
import { sendDiceValues } from '../../store/state';

const Dices = () => {
  const { activePlayer, currentPlayer, diceValues } = useSelector(({ state }) => state);
  const [diceValue1, diceValue2] = diceValues;
  const reactDice = useRef(null)
  const dispatch = useDispatch();
  const [isRolling, setRolling] = useState(false);
  
  const onRollDice = () => {
    if (isRolling || (activePlayer > 0 && currentPlayer !== activePlayer)) {
      return;
    }
    setRolling(true)
    const value1 = Math.floor(Math.random() * 6) + 1;
    const value2 = Math.floor(Math.random() * 6) + 1;
    reactDice.current?.rollAll([value1, value2]);
    dispatch(sendDiceValues([value1, value2]));
  }
  
  useEffect(() => {
    reactDice.current?.rollAll([diceValue1, diceValue2]);
  }, [diceValue1, diceValue2])

  return (
    <div className="dices" onClick={onRollDice}>
      <ReactDice
        disableIndividual
        outline
        dieSize={50}
        numDice={2}
        margin={5}
        rollTime={1}
        outlineColor="#aaaaaa"
        dotColor="#000000"
        faceColor="#ffffff"
        ref={reactDice}
        rollDone={() => setRolling(false)}
      />
    </div>
  );
}

export default Dices;
