import { useSelector, useDispatch } from 'react-redux';
import Dices from './dices.component';
import { restartGame } from '../../store/state';
import './center.styles.css';

const Center = () => {
  const dispatch = useDispatch();
  const { activePlayer, players } = useSelector(({ state }) => state);

  const restart = () => {
    if (window.confirm('    ???')) {
      dispatch(restartGame());
      window.location.reload();
    }
  }

  return (
    <div className="grid-center">
      <div>
        <button className="button" onClick={restart}></button>
      </div>
      <div className={`players p${activePlayer}`}>
        {players.length < 2 ? '...' : (
          activePlayer === 0 ? ' ?' : `  #${activePlayer}`
        )}
      </div>
      <Dices />
    </div>
  );
};

export default Center;
