import { useEffect, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  setChipPositions,
  setDiceValues,
  setPlayers,
  setActivePlayer,
  setCurrentPlayer,
} from '../../store/state';
import { ws } from '../../store';
import Grid from '../grid/grid.component';
import GridCenter from '../center/center.component';
import './app.styles.css';

const App = () => {
  let userID = localStorage.getItem('userID');
  if (!userID) {
    userID = Date.now() + Math.floor(Math.random() * (99999 - 1)) + 1;
    localStorage.setItem('userID', userID);
  }
  const dispatch = useDispatch();
  const { players, chipPositions, activePlayer, currentPlayer, diceValues } = useSelector(({ state }) => state);
    
  const updateData = useCallback((data) => {
    if (data.diceValues) {
      if (diceValues[0] === data.diceValues[0] && diceValues[1] === data.diceValues[1]) {
        dispatch(setDiceValues([0, 0]));
        setTimeout(() => dispatch(setDiceValues(data.diceValues)), 1);
      }
      else {
        dispatch(setDiceValues(data.diceValues));
      }
    }
    if (data.chipPositions && JSON.stringify(data.chipPositions) !== JSON.stringify(chipPositions)) {
      dispatch(setChipPositions(data.chipPositions));
    }
    if (data.players && JSON.stringify(data.players) !== JSON.stringify(players)) {
      dispatch(setPlayers(data.players));
    }
    if (data.activePlayer && data.activePlayer !== activePlayer) {
      dispatch(setActivePlayer(data.activePlayer));
    }
  }, [dispatch, players, chipPositions, activePlayer, diceValues]);

  useEffect(() => {
    ws.onopen = () => {
      console.log('connect', 'send UserID', userID);
      ws.send(JSON.stringify({ userID }));
    };
    
    ws.onmessage = e => {
      console.log('receive from server', JSON.parse(e.data));
      const { name, state, changedData } = JSON.parse(e.data);
      if (name === 'clean') {
        setTimeout(() => window.location.reload(), 500);
        return;
      }
      if (state) {
        updateData(state);
      }
      if (changedData) {
        updateData(changedData);
      }
    };
    
    ws.onerror = e => {
      console.log('error from server:', e, e.message);
    };
    
    ws.onclose = e => {
      console.log(e.code, e.reason);
    };
  }, [updateData, currentPlayer, userID]);

  useEffect(() => {
    const alreadyLogin = players.find((player) => player.userID ===  userID);
    console.log('alreadyLogin', alreadyLogin);
    if (currentPlayer === 0 && alreadyLogin) {
      dispatch(setCurrentPlayer(alreadyLogin.id));
    }

    const intervalID = setInterval(() => {
      console.log('try send userID', ws.readyState);
      if (ws.readyState === 1) {
        ws.send(JSON.stringify({ userID }));
        clearInterval(intervalID);
      }
    }, 500);
  }, [currentPlayer, dispatch, players, userID]);

  return (
    <div className="app">
      <Grid />
      <GridCenter />
    </div>
  );
}

export default App;
