import { useDispatch } from 'react-redux';
import { onPressCell } from '../../../store/state';
import { ReactComponent as ChipSVG } from '../../../assets/chip.svg';
import './chip.styles.css';

const Chip = (props) => {
  const dispatch = useDispatch();
  const {
    id,
    active,
    player,
    position: { left, top },
  } = props;
  
  const handleClickCell = () => {
    console.log('click', id);
    dispatch(onPressCell(id))
  };

  let className = 'chip';

  if (player !== undefined) {
    className += ` p${player}`;
  }

  if (active) {
    className += ' active';
  }
  return <ChipSVG className={className} style={{ left: `${left}px`, top: `${top}px` }} onClick={handleClickCell}/>
}

export default Chip;