import { configureStore } from '@reduxjs/toolkit';
import state from './state';

export const ws = new WebSocket(`ws://${window.location.hostname}:3000`);

export const sendData = (storeAPI) => (next) => (action) => {
  next(action);
  const {
    state: {
      players,
      activePlayer,
      chipPositions,
    },
  } = storeAPI.getState();
  
  switch (action.type) {
    case 'state/restartGame':
      ws.send(JSON.stringify({name: 'clean'}));
      break;
    case 'state/setPlayers':
      ws.send(JSON.stringify({players}));
      break;
    case 'state/setActivePlayer':
      ws.send(JSON.stringify({activePlayer}));
      break;
    case 'state/onPressCell':
      ws.send(JSON.stringify({chipPositions}));
      break;
    case 'state/sendDiceValues':
      ws.send(JSON.stringify({diceValues: action.payload}));
      break;
    default:
      break;
  }
}

export default configureStore({
  reducer: { state },
  middleware: [sendData],
});
