import { useSelector } from 'react-redux';
import Chip from './chip.component';

const Chips = (props) => {
  const { gridRef } = props;
  const { activeChip, chipPositions, players } = useSelector(({ state }) => state);

  return chipPositions.map((chips, playerIndex) => {
    if (!players.find((player) => player.id === (playerIndex + 1))) {
      return null;
    }
    return chips.map((id, key) => {
      const cell = gridRef?.current?.querySelector(`[data-id="${id}"]`);
      return (
        <Chip
          key={key}
          id={id}
          active={activeChip === id}
          player={playerIndex + 1}
          position={{ left: cell.offsetLeft, top: cell.offsetTop }}
        />
      );
    })
  });
}

export default Chips;