import { createSlice } from '@reduxjs/toolkit';

export const slice = createSlice({
  name: 'state',
  initialState: {
    players: [],
    activePlayer: 0,
    currentPlayer: 0,
    diceValues: [6, 6],
    activeChip: null,
    chipPositions: [
      [-1.1, -1.2, -1.3, -1.4],
      [-2.1, -2.2, -2.3, -2.4],
      [-3.1, -3.2, -3.3, -3.4],
      [-4.1, -4.2, -4.3, -4.4],
    ],
  },
  reducers: {
    restartGame: (state) => ({ ...state }),
    setPlayers: (state, { payload: players }) => ({...state, players}),
    setCurrentPlayer: (state, { payload: currentPlayer }) => ({...state, currentPlayer}),
    setActivePlayer: (state, { payload: activePlayer }) => ({...state, activePlayer}),
    setDiceValues: (state, { payload: diceValues }) => ({...state, diceValues}),
    sendDiceValues: (state, { payload: diceValues }) => ({...state, diceValues}),
    setChipPositions: (state, { payload: chipPositions }) => ({...state, chipPositions}),
    onPressCell: (state, { payload: cellID }) => {
      console.log('press', cellID, state.activeChip);
      if (cellID === state.activeChip) {
        return { ...state, activeChip: null };
      }

      if (state.chipPositions[state.currentPlayer - 1]?.includes(cellID)) {
        return { ...state, activeChip: cellID };
      }

      if (state.activeChip !== null) {
        const chipPositions = state.chipPositions.map((positions, playerIndex) => {
          if (positions.includes(cellID)) {
            if (playerIndex !== state.currentPlayer - 1) {
              console.log('');
              return positions.map((id, index) => {
                if (id === cellID) {
                  return `-${state.currentPlayer}.${playerIndex + 1}.${index + 1}`;
                }
                return id;
              });
            }
          }
          
          if (playerIndex === state.currentPlayer - 1) {
            return positions.map((id) => {
              if (id === state.activeChip) {
                return cellID;
              }
              return id;
            });
          }
          return positions;
        })
        return { ...state, chipPositions, activeChip: null };
      }
      return { ...state };
    }
  }
})

export const {
  restartGame,
  setPlayers,
  setCurrentPlayer,
  setActivePlayer,
  setDiceValues,
  sendDiceValues,
  setChipPositions,
  onPressCell,
  feedChip,
} = slice.actions

export default slice.reducer